import React from "react";

export const FormControl = (Element) => ({ input, meta, ...props }) => {
  return (
    <div>
      <div>
        <Element {...input} {...props} />;
      </div>

      {meta.touched && meta.error && <span>{meta.error}</span>}
    </div>
  );
};

export const Textarea = FormControl("textarea");
export const Input = FormControl("input");
